<template>
  <panel title=" แก้ไขข้อมูลสินค้า">
    <template v-slot:header-right> </template>

    <div class="vx-row">
      <custom-tree-select
        url="/api/setting/master/product"
        :col="6"
        v-model="form.setting_master_product_id"
      />
      <form-input
        label="รหัสสินค้า"
        v-model="form.code"
        v-validate="`required`"
        :col="3"
      />
      <form-input
        label="ชื่อสินค้า(ภาษาไทย)"
        v-model="form.setting_master_product.name_th"
        v-validate="`required`"
        :col="3"
      />
      <form-input
        label="ชื่อสินค้า(ภาษาอังกฤษ)"
        v-model="form.setting_master_product.name_en"
        v-validate="`required`"
        :col="3"
      />
      <form-input
        label="รายละเอียด"
        v-model="form.description"
        v-validate="`required`"
        :col="3"
      />
    </div>
    <!-- form กรณีเป็นอะไหล่ ตัวรถ-->
    <div class="vx-row" v-if="form.setting_master_product.type == 'ตัวรถ'">
      <form-input
        label="รหัสเครื่อง"
        v-model="form.vin"
        v-validate="`required`"
        :col="3"
      />
      <form-input
        label="ราคาขายปลีก"
        v-model="form.retail_price"
        v-validate="`required`"
        :col="3"
      />
      <form-input
        label="ราคาขายส่ง"
        v-model="form.wholesale_price"
        v-validate="`required`"
        :col="3"
      />
      <form-tag :col="6" v-model="form.tags"></form-tag>
    </div>
    <!-- form กรณีเป็นอะไหล่ -->
    <div class="vx-row" v-if="form.setting_master_product.type == 'อะไหล่'">
      <form-input
        v-if="form.setting_master_product.type == 'อะไหล่'"
        label="จำนวน"
        v-model="form.quantity"
        v-validate="`required`"
        :col="3"
      />
      <form-input
        label="ราคาขายปลีก"
        v-model="form.retail_price"
        v-validate="`required`"
        :col="3"
      />
      <form-input
        label="ราคาขายส่ง"
        v-model="form.wholesale_price"
        v-validate="`required`"
        :col="3"
      />
      <form-tag :col="6" v-model="form.tags"></form-tag>
    </div>
    <!-- form กรณีเป็นอะไหล่ -->
    <div class="vx-row" v-if="form.setting_master_product.type == 'ต่อพ่วง'">
      <form-input
        v-if="form.setting_master_product.type == 'อะไหล่'"
        label="จำนวน"
        v-model="form.quantity"
        v-validate="`required`"
        :col="3"
      />
      <form-input
        label="ราคาขายปลีก"
        v-model="form.retail_price"
        v-validate="`required`"
        :col="3"
      />
      <form-input
        label="ราคาขายส่ง"
        v-model="form.wholesale_price"
        v-validate="`required`"
        :col="3"
      />
      <form-tag :col="6" v-model="form.tags"></form-tag>
    </div>
    <div>
      <attached
        :attached_id="form.id"
        :model="data_model"
        ref="upload"
      ></attached>
    </div>
    <br />

    <vs-row>
      <vs-col vs-type="flex" vs-justify="flex-end" vs-align="center">
        <vs-button class="mr-3" @click="openAlert()">บันทึก</vs-button>
        <router-link to="/product/checkWarehouse">
          <vs-button color="danger" type="filled">ยกเลิก</vs-button>
        </router-link>
      </vs-col>
    </vs-row>
    <pre>{{ form }}</pre>
    setting_master_product_id
    <pre>{{ form.setting_master_product_id }}</pre>
    <pre>---------------------------</pre>
    setting_master_product.id
    <pre>{{ form.setting_master_product.id }}</pre>
  </panel>
</template>

<script>
export default {
  data () {
    return {
      id: '',
      attached_id: '',
      data_model: 'App\\Models\\Product\\Product',

      form: {
        code: '',
        vin: '',
        name_th: '',
        name_en: '',
        tags: [],
        description: '',
        quantity: '',
        retail_price: '',
        wholesale_price: '',
        productInsert: '',
        setting_master_product_id: '',
        setting_master_product: {
          name_th:'',
          name_en:''
        }
      }
    }
  },
  methods: {
    setData () {
      this.form.id = this.$route.query.id
    },
    async loadData () {
      try {
        const { data } = await this.$http.get(`/api/product/${this.form.id}`)

        this.form = data.data
        this.attached_id = data.data.id
      } catch (error) {
        throw error
      }
    },
    async openAlert () {
      try {
        const result = await this.$validator.validateAll()
        if (result) {
          this.$vs.dialog({
            type: 'confirm',
            color: 'success',
            title: 'คุณต้องการแก้ไขข้อมูลหรือไม่',
            text: `แก้ไขข้อมูลสินค้า  ${this.form.setting_master_product.name_th} ( ${this.form.setting_master_product.name_en} )`,
            acceptText: 'ยืนยัน',
            cancelText: 'ยกเลิก',
            accept: this.acceptEdit
          })
        } else {
          this.$vs.dialog({
            color: 'danger',
            title: 'แก้ไขข้อมูลไม่สำเร็จ',
            text: 'กรุณาตรวจสอบการกรอกข้อมูลให้ครบถ้วน',
            acceptText: 'ปิด',
            accept: this.acceptAlert
          })
        }
      } catch (error) {
        this.$vs.dialog({
          color: 'danger',
          title: 'แก้ไขข้อมูลไม่สำเร็จ',
          text: 'กรุณาตรวจสอบการกรอกข้อมูลให้ครบถ้วน',
          acceptText: 'ปิด',
          accept: this.acceptAlert
        })
      }
    },
    async acceptEdit () {
      const resp = await this.$http.patch(
        `/api/product/${this.form.id}`,
        this.form
      )

      this.attached_id = resp.data.data.id
      this.$refs.upload.id = resp.data.data.id

      const first = await this.$refs.upload.submit()
      console.log(first)
      this.$vs.notify({
        color: 'success',
        title: 'ยืนยันการแก้ไขข้อมูล',
        text: 'แก้ไขข้อมูลสำเร็จ'
      })
      this.$router.push({ name: 'product_index', query: {} })
    }
  },
  mounted () {
    this.setData()
    setTimeout(() => {
      this.loadData()
    }, 0)
  }
}
</script>


